@import url(https://fonts.googleapis.com/css?family=Mountains+of+Christmas:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #252525 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
  width:100%;
  min-height:50px;
  box-sizing: content-box;
}

.footer img {
  position: relative;
  -webkit-animation: santa 4s linear infinite;
          animation: santa 4s linear infinite;
  height:50px;
}

@-webkit-keyframes santa {
  0% {-webkit-transform: scaleX(1);left:0px; top:0px;}
  50% {-webkit-transform: scaleX(1);left:calc(100vw - 115px); top:0px;}
  51% {-webkit-transform: scaleX(-1);}
  100% {-webkit-transform: scaleX(-1); left:0px}
}

@keyframes santa {
  0% {-webkit-transform: scaleX(1);left:0px; top:0px;}
  50% {-webkit-transform: scaleX(1);left:calc(100vw - 115px); top:0px;}
  51% {-webkit-transform: scaleX(-1);}
  100% {-webkit-transform: scaleX(-1); left:0px}
}

.christmas-blue {
  color: #035ee2;
    background: -webkit-linear-gradient(transparent, transparent), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/191814/blue_glitter.gif) repeat;
    /*background: -o-linear-gradient(transparent, transparent) !important;*/
    -webkit-background-clip: text !important;
    -webkit-text-fill-color: transparent !important;
    margin: 0;
    padding: 0;
  
    font-weight: 900;
    width: 100%;
    text-align: center;
    letter-spacing: 1px;
    z-index: 999999;
    -webkit-background-clip: text;
  }
  
  .christmas-gold {
    color: gold;
    background: -webkit-linear-gradient(transparent, transparent), url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/191814/gold_glitter.gif) repeat;
    /*background: -o-linear-gradient(transparent, transparent) !important;*/
    -webkit-background-clip: text !important;
    -webkit-text-fill-color: transparent !important;
    margin: 0;
    padding: 0;
    font-weight: 900;
    width: 100%;
    text-align: center;
    letter-spacing: 1px;
    z-index: 999999;
    -webkit-background-clip: text;
  }
  
  
  
  h1 {position:absolute;
    font-size: 8.5vw;
    width:100%;
    height:100%;
    text-align:center;
    max-height:100px;
    margin-top:150px;
    color:#fff;
    font-family: 'Mountains of Christmas', cursive;
  }
  


